.down-slider {
   margin-top: 80px;
   min-width: 0;
   overflow: hidden;
   margin-right: -300px;
}
.down {

		&__wrapper {
		}

		&__slide {

		}

		&__img {


         & img {
            width: 100%;
            height: 100%;
            background-size: cover;
         }
		}
}
.down-swiper-buttons {
   margin-top: 30px;
   display: flex;
   align-items: center;
   justify-content: center;
   width: 110px;
   height: 40px;
   position: relative;
}
.slider-pagination-down {
   position: relative;
   left: 50;
   width: 40px;
   height: 40px;
   //transform: translate(-50%, 0);
}
.swiper-pagination-down {
   content: "";
   position: absolute;
   top: 50;
}
.swiper-button-prev-down {
   position: absolute;
   top: 50%;
   left: 0px;
   transform: translate(0, -50%);
   width: 31px;
   height: 8px;
   background: url('../../img/slider/arrow-slider.svg');
}
.swiper-button-next-down {
   position: absolute;
   //top: 50%;
   right: 0px;
   transform: translate(0, -50%);
   width: 31px;
   height: 8px;
   transform: rotate(180deg);
   background: url('../../img/slider/arrow-slider.svg');
}
